body {
  margin: 0;
  padding: 0;
  font-family:Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;

  &__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
  }
  &__list {
    display: flex;
    list-style: none;
    background: green;
    margin: 0;
    padding: 0;
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    gap: 5px;
  }
}
